import { computed } from "vue";

@Options({
  components: {
    // HelloWorld,
  },
})
export default class App extends Vue {
  async startAuth() {
    openAuthWin(true);
  }
  refresh_token = computed(() => state.auth.refresh_token);
}
</script>

