const client_id = 'f20605cf-927f-4f57-bfef-3b1738ed0871';
const client_secret = 'rVsI96_DUWbz3V15M2CP2IMv3-2c_~.5.X';
// const redirect_uri = 'https://dev.songe.li/';
const redirect_uri = `${location.origin}${location.pathname}`;
// const response_type = 'code';
const scope = ['files.read.all', 'offline_access'].join('\x20');

function getAuthUrl(): string {
    const response_type = 'code';
    const response_mode = 'fragment';
    const authUrl = new URL(
        'https://login.microsoftonline.com/common/oauth2/v2.0/authorize'
    );
    const authParams = {
        client_id,
        redirect_uri,
        response_type,
        scope,
        response_mode,
    };
    Object.entries(authParams).forEach(([key, value]) => {
        authUrl.searchParams.set(key, value);
    });
    return authUrl.href;
}

interface Auth {
    token_type: string;
    scope: string;
    expires_in: number;
    ext_expires_in: number;
    access_token: string;
    refresh_token: string;
    mark?: '6807365a';
}

async function getAuth(code: string): Promise<Auth> {
    const fd = new FormData();
    const data = {
        client_id,
        redirect_uri,
        client_secret,
        code,
        grant_type: 'authorization_code',
    };
    Object.entries(data).forEach(([key, value]) => {
        fd.set(key, value);
    });
    const response = await fetch(
        'https://login-microsoftonline-com.lisonge.workers.dev/common/oauth2/v2.0/token',
        {
            method: 'POST',
            body: fd,
        }
    );
    const auth = (await response.json()) as Auth;
    auth.mark = '6807365a';
    return auth;
}

function openAuthWin(useMinWin: boolean = false) {
    const u = getAuthUrl();
    if (!useMinWin) {
        window.open(u);
    } else {
        const features = {
            width: screen.availWidth / 4,
            height: screen.availHeight / 1.5,
            left: 0,
            top: 0,
        };
        features.left = screen.availWidth / 2 - features.width / 2;
        features.top = screen.availHeight / 2 - features.height / 2;
        window.open(
            u,
            '',
            Object.entries(features)
                .map(([key, value]) => `${key}=${value}`)
                .join(',')
        );
    }
}

export { getAuth, Auth, openAuthWin };
