import { createStore } from 'vuex';
import { Auth } from '@/common';

const store = createStore({
    state: {
        auth: {
            token_type: '',
            scope: '',
            expires_in: 3600,
            ext_expires_in: 3600,
            access_token: '',
            refresh_token: '',
        },
    },
    mutations: {
        assignAuth(state, payload:{auth:Auth}) {
            Object.assign(state.auth, payload.auth);
        },
    },
    actions: {},
    modules: {},
});
const { state } = store;

export { state };
export default store;
