import { createApp } from 'vue';
import App from './App.vue';
import { getAuth, Auth } from './common';
import store from './store';

const sp = new URLSearchParams(location.hash.substring(1));
let closeWaitSecond = 3;
const app = document.querySelector('#app') as HTMLDivElement;
if (sp.has('error')) {
    setTimeout(window.close, 1000 * closeWaitSecond);
    setInterval(() => {
        app.innerText = `获取授权令牌失败\n${closeWaitSecond}秒后自动关闭窗口`;
        closeWaitSecond -= 1;
    }, 1000);
} else if (sp.has('code')) {
    const code = sp.get('code')!;
    app.innerText = '获取授权令牌中';
    const handler = async () => {
        let auth = await getAuth(code);
        window.opener.postMessage(auth, location.origin);
        setTimeout(window.close, 1000 * closeWaitSecond);
        setInterval(() => {
            app.innerText = `获取授权令牌成功\n${closeWaitSecond}秒后自动关闭窗口`;
            closeWaitSecond -= 1;
        }, 1000);
    };
    handler();
} else {
    createApp(App)
        .use(store)
        .mount('#app');
    addEventListener('message', (ev) => {
        if (ev.origin != location.origin || typeof ev.data != 'object') {
            return;
        }
        const auth = ev.data as Auth;
        console.log(auth);
        if (auth.mark == '6807365a') {
            store.commit('assignAuth', { auth });
        }
    });
}


